@import "../constants/mixins";
@import "../constants/spaces";
@import "../constants/fonts";

.menu-container {
  padding: $xxxLarge $huge 0 $huge;
  position: sticky;
  top: 0;
  z-index: 1;

  .menu__list {
    position: absolute;
    width: 300px;
    margin-top: -100px;
    padding: $xGiant 0 $large 0;
    border-radius: 14px;
    background: #e0d9cc;
    list-style-type: none;
    -webkit-font-smoothing: antialiased;
    /* to stop flickering of text in safari */

    &:lang(en) {
      left: -300px;
    }

    &:lang(fa) {
      right: -300px;
    }

    transition: all 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);

    .menu__item {
      display: block;
      padding: 0;
      &:lang(fa) {
        font-size: 26px;
      }
      .menu__link {
        font-weight: bold;
        display: block;
        padding: $small;
        cursor: pointer;
        user-select: none;
        -webkit-touch-callout: none;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

        position: relative;
        display: block;
        text-align: center;
        color: #929292;
        transition: color 0.4s;
        text-decoration: none;

        &:hover,
        &:focus {
          outline: none;
          color: #d94f5c;
        }
      }
    }

    .menu__item--current {
      .menu__link {
        color: #d94f5c;
      }
    }
  }
}

// min-screen: 992px;
@include lg {
  .menu-container {
    position: relative;
    padding: 0;
    padding-top: $medium;
    .menu__list {
      position: relative;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      margin: 0;
      padding: 0;
      list-style: none;

      width: unset;
      background: unset;
      transition: unset;

      &:lang(en),
      &:lang(fa) {
        left: unset;
        right: unset;
      }

      .menu__item {
        display: flex;
        align-items: center;
        margin: $medium 0;
      }
    }
  }

  .menu__link {
    margin: 0 $medium;
    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #d94f5c;
      transform: scale3d(0, 1, 1);
      transition: transform 0.2s;
    }

    &::before {
      top: 0;
      transform-origin: 0 50%;
    }

    &::after {
      bottom: 0;
      transform-origin: 100% 50%;
    }
  }

  .menu__item--current {
    .menu__link::before,
    .menu__link::after {
      transform: scale3d(1, 1, 1);
      transition-timing-function: cubic-bezier(0.22, 0.61, 0.36, 1);
      transition-duration: 0.4s;
    }
  }
}
